import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { QueryService } from 'src/app/services/query.service';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { LoginI } from 'src/app/interfaces/login-i';
import { ErrorI } from 'src/app/interfaces/error-i';

declare const $: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  private user = {
    user_name: '',
    password: ''
  }

  private error: string = '';

  constructor(private spinner: NgxSpinnerService, private _query: QueryService, private _auth: AuthService, private _router: Router) {
    if (this._auth.isAuth()) this._auth.navigateHome();
  }

  ngOnInit() {
  }

  login() {
    this.spinner.show();
    let query = {
      query: `query Login($user_name: String!, $password: String!){
        login(user_name: $user_name, password: $password){
          userId
          token
          tokenExpiration
        }
      }`,
      variables: {
        ...this.user
      }
    };
    this._query.getData(query).subscribe((result: LoginI) => {
      this.spinner.hide();
      this._auth.login(result);
    }, (error: ErrorI) => {
      this.error = error.error.errors[0].message;
      this.spinner.hide();
    });
  }

}
