import { Injectable } from '@angular/core';
import { QueryService } from './query.service';
import { Router } from '@angular/router';
import { LoginI } from '../interfaces/login-i';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private _query: QueryService, private _router: Router) { }

  navigateHome() {
    this._router.navigate(['/home']);
  }

  login(login: LoginI) {
    localStorage.setItem('userId', login.data.login.userId);
    localStorage.setItem('token', 'Bearer ' + login.data.login.token);
    this.navigateHome();
  }

  navigateLogin() {
    this._router.navigate(['/login']);
  }

  logout() {
    localStorage.clear();
    this.navigateLogin();
  }

  isAuth(): boolean {
    if (!localStorage.getItem('userId') || !localStorage.getItem('token')) {
      this.navigateLogin();
      return false
    };
    return true;
  }
  isAuthCreate(): boolean {
    if (!localStorage.getItem('userId') || !localStorage.getItem('token')) {
      return false;
    };
    return true;
  }
}
