import { Component, OnInit } from '@angular/core';
import { Product } from 'src/app/models/product';
import { Material } from 'src/app/models/material';
import { NgxSpinnerService } from 'ngx-spinner';
import { QueryService } from 'src/app/services/query.service';
import { ProductsI } from 'src/app/interfaces/products-i';
import { ErrorI } from 'src/app/interfaces/error-i';
import { ProductI } from 'src/app/interfaces/product-i';
import { Router } from '@angular/router';
import { Category } from 'src/app/models/category';
import { CreateProductI } from 'src/app/interfaces/create-product-i';

@Component({
  selector: 'app-product-create',
  templateUrl: './product-create.component.html',
  styleUrls: ['./product-create.component.scss']
})
export class ProductCreateComponent implements OnInit {

  private productToSave: Product;
  private materialsForSelect: Product[];
  private error: string;
  private selectedUnits: number;
  private selectedMaterial: Product;

  constructor(private spinner: NgxSpinnerService, private _query: QueryService, private _router: Router) {
    this.productToSave = new Product();
    this.selectedUnits = 1;
    this.fillMaterialsForSelect();
    let category = new Category();
    category._id = "5d1a7052abe3ea3b10708334";
    category.name = "Uncategorized";
    category.products = null;
    category.description = "";
    this.productToSave.category = category;
  }

  fillMaterialsForSelect() {
    this.spinner.show();
    let query = {
      query: `query Products{
        products{
          ...Material
          materials {
            units
            product {
              ...Material
              materials {
                units
                product {
                  ...Material
                  materials {
                    units
                    product {
                      ...Material
                      materials {
                        units
                        product {
                          ...Material
                          materials {
                            units
                            product {
                              ...Material
                              materials {
                                units
                                product {
                                  ...Material
                                  materials {
                                    units
                                    product {
                                      ...Material
                                      materials {
                                        units
                                        product {
                                          ...Material
                                          materials{
                                            units
                                            product{
                                              ...Material
                                              materials{
                                                units
                                                product{
                                                  ...Material
                                                }
                                              }
                                            }
                                          }
                                        }
                                      }
                                    }
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      fragment Material on Product {
        _id
        name
        description
      }`
    };
    this._query.getData(query).subscribe((result: ProductsI) => {
      this.materialsForSelect = result.data.products;
      this.selectedMaterial = this.materialsForSelect[0];
      this.spinner.hide();
    }, (error: ErrorI) => {
      this.error = error.error.errors[0].message;
      this.spinner.hide();
    });
  }

  ngOnInit() {
  }

  addMaterial() {
    if (this.materialsForSelect !== undefined) {
      let materilasToSave = this.productToSave.materials;

      let aux1 = new Material();
      aux1.units = this.selectedUnits;
      aux1.product = this.selectedMaterial;
      aux1.description = "";

      if (materilasToSave !== undefined) {
        this.productToSave.materials = [...materilasToSave, aux1];
      } else {
        this.productToSave.materials = [aux1];
      }
      let auxMat: Product[];
      for (let i = 0; i < this.materialsForSelect.length; i++) {
        if (aux1.product._id != this.materialsForSelect[i]._id)
          if (auxMat !== undefined) {
            auxMat = [...auxMat, this.materialsForSelect[i]];
          } else {
            auxMat = [this.materialsForSelect[i]]
          }
      }
      this.materialsForSelect = auxMat;
      if (this.materialsForSelect !== undefined) this.selectedMaterial = this.materialsForSelect[0];
      this.selectedUnits = 1;
    }
  }

  removeMaterial(material: Material) {
    let auxMaterialsToSave: Material[] = this.productToSave.materials;
    let auxNewMaterials: Material[];

    for (let i = 0; i < this.productToSave.materials.length; i++) {
      if (material.product._id != this.productToSave.materials[i].product._id)
        if (auxNewMaterials !== undefined) {
          auxNewMaterials = [...auxNewMaterials, this.productToSave.materials[i]];
        } else {
          auxNewMaterials = [this.productToSave.materials[i]];
        }
    }
    this.productToSave.materials = auxNewMaterials;

    let auxProduct: Product = material.product;
    if (this.materialsForSelect !== undefined) {
      let auxMaterials: Product[] = this.materialsForSelect;
      this.materialsForSelect = [...auxMaterials, auxProduct];
    } else {
      this.materialsForSelect = [auxProduct];
    }
    this.selectedMaterial = this.materialsForSelect[0];
  }

  save() {
    if (this.productToSave.name === undefined || this.productToSave.name == '') {
      this.error = "Nombre de producto es requerido.";
      return;
    }
    let aux = this.productToSave;
    this.spinner.show();
    let query = {
      query: `mutation CreateProduct($productInput: ProductInput!) {
        createProduct(productInput: $productInput) {
          _id
        }
      }`,
      variables: {
        productInput: {
          ...aux,
          category: aux.category._id,
          materials: this.transformMaterial(aux.materials)
        }
      }
    };
    this._query.getData(query).subscribe((result: CreateProductI) => {
      this.spinner.hide();
      this._router.navigate(['/products', result.data.createProduct._id]);
    }, (error: ErrorI) => {
      this.error = error.error.errors[0].message;
      this.spinner.hide();
    });
  }

  transformMaterial(mtrls: Material[]) {
    if (mtrls === undefined) return [];

    let materials;
    for (let i = 0; i < mtrls.length; i++) {
      let item = {
        units: mtrls[i].units,
        product: mtrls[i].product._id,
        description: mtrls[i].description
      };
      if (materials !== undefined) {
        materials = [...materials, item];
      } else {
        materials = [item];
      }
    }
    return materials;
  }

}
