import { Component, OnInit } from '@angular/core';
import { Product } from 'src/app/models/product';
import { QueryService } from 'src/app/services/query.service';
import { ProductsI } from 'src/app/interfaces/products-i';
import { NgxSpinnerService } from 'ngx-spinner';
import { ErrorI } from 'src/app/interfaces/error-i';
import { variable } from '@angular/compiler/src/output/output_ast';
import { ProductI } from 'src/app/interfaces/product-i';

declare const $: any;

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss']
})
export class ProductListComponent implements OnInit {

  private search: string;
  private error: string;
  private products: Product[];
  private product: Product;

  constructor(private _query: QueryService, private spinner: NgxSpinnerService) {
    this.spinner.show();
    let query = {
      query: `query Producst{
      products {
        _id
        name
        category{
          name
        }
      }
    }`};
    this._query.getData(query).subscribe((products: ProductsI) => {
      this.products = products.data.products;
      this.spinner.hide();
    }, (error: ErrorI) => {
      this.error = error.error.errors[0].message;
      this.spinner.hide();
    });
  }

  ngOnInit() {
  }

  viewTreeModal(_id: string) {
    this.spinner.show();
    let query = {
      query: `query Product($_id: ID!) {
        product(_id: $_id) {
          ...Material
          materials {
            units
            product {
              ...Material
              materials {
                units
                product {
                  ...Material
                  materials {
                    units
                    product {
                      ...Material
                      materials {
                        units
                        product {
                          ...Material
                          materials {
                            units
                            product {
                              ...Material
                              materials {
                                units
                                product {
                                  ...Material
                                  materials {
                                    units
                                    product {
                                      ...Material
                                      materials {
                                        units
                                        product {
                                          ...Material
                                          materials{
                                            units
                                            product{
                                              ...Material
                                              materials{
                                                units
                                                product{
                                                  ...Material
                                                }
                                              }
                                            }
                                          }
                                        }
                                      }
                                    }
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      
      fragment Material on Product {
        _id
        name
      }`, variables: { _id }
    };
    this._query.getData(query).subscribe((result: ProductI) => {
      this.product = result.data.product;
      this.spinner.hide();
      $('#modal').modal('handleUpdate');
      $('#modal').modal('show');
    }, (error: ErrorI) => {
      this.error = error.error.errors[0].message;
      this.spinner.hide();
    });
  }

  productLoop(_id: string, products: Product[]) {
  }

}
