import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Product } from 'src/app/models/product';
import { QueryService } from 'src/app/services/query.service';
import { ProductI } from 'src/app/interfaces/product-i';
import { NgxSpinnerService } from 'ngx-spinner';
import { ErrorI } from 'src/app/interfaces/error-i';
import { Material } from 'src/app/models/material';
import { Request } from 'src/app/models/request';

declare const $: any;

@Component({
  selector: 'app-product-detail',
  templateUrl: './product-detail.component.html',
  styleUrls: ['./product-detail.component.scss']
})
export class ProductDetailComponent implements OnInit {

  private product: Product;
  private _ids: string[];
  private error: string;
  private limit: number;
  private products: Product[];

  constructor(private route: ActivatedRoute, private _query: QueryService, private spinner: NgxSpinnerService, private _router: Router) { }

  ngOnInit() {
    this.route.paramMap.subscribe(params => {
      let query = {
        query: `query Product($_id: ID!) {
          product(_id: $_id) {
            _id
            name
            direct_labor
            ss
            keep_cost
            category{
              _id
              name
            }
            requests {
              _id
              requested_to
              state
            }
            description
            materials {
              units
              product {
                ...Material
                materials {
                  units
                  product {
                    ...Material
                    materials {
                      units
                      product {
                        ...Material
                        materials {
                          units
                          product {
                            ...Material
                            materials {
                              units
                              product {
                                ...Material
                                materials {
                                  units
                                  product {
                                    ...Material
                                    materials {
                                      units
                                      product {
                                        ...Material
                                        materials {
                                          units
                                          product {
                                            ...Material
                                            materials{
                                              units
                                              product{
                                                ...Material
                                                materials{
                                                  units
                                                  product{
                                                    ...Material
                                                  }
                                                }
                                              }
                                            }
                                          }
                                        }
                                      }
                                    }
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
        
        fragment Material on Product {
          _id
          name
        }`, variables: {
          _id: params.get('id')
        }
      };
      this.spinner.show();
      this._query.getData(query).subscribe((result: ProductI) => {
        this.product = result.data.product;
      }, (error: ErrorI) => {
        this.error = error.error.errors[0].message;
        this.spinner.hide();
      }, () => {
        this._ids = [this.product._id];

        this.loopIds(this.product.materials);

        let aux = this.getLastDemand(this.product);
        if (aux == null) {
          this.limit = 0;
        } else {
          let toDay = new Date();
          let limit = new Date(aux.requested_to);
          this.limit = Math.ceil((limit.getTime() - toDay.getTime()) / (1000 * 60 * 60 * 24)) + 1;
        }

        this.spinner.hide();
      });
    });
  }

  getLastDemand(product: Product): Request {
    let lastRequest = product.requests[0];
    if (lastRequest === undefined) return null;

    for (let i = 0; i < product.requests.length; i++) {
      let requestI = product.requests[i];

      if (requestI.state != 'Canceled') {
        let dateLast = new Date(lastRequest.requested_to);
        let dateI = new Date(requestI.requested_to);

        if (dateLast < dateI) lastRequest = requestI;
      }
    }
    return lastRequest;
  }

  loopIds(materials: Material[]) {
    if (materials === undefined || materials.length === 0) return;

    for (let i = 0; i < materials.length; i++) {
      const material = materials[i];

      let can = true;
      let ids = this._ids;
      for (let j = 0; j < ids.length; j++) {
        if (material.product._id == ids[j]) {
          can = false;
        }
      }
      if (can) {
        ids = [...ids, material.product._id];
        this._ids = [...ids];
      };

      if (material.product.materials !== undefined && material.product.materials.length > 0) {
        this.loopIds(material.product.materials);
      }
    }
  }

  storedProduct(prodcutOutput) {
    if (this.products === undefined) { this.products = [prodcutOutput]; } else { this.products = [...this.products, prodcutOutput]; }
  }

  createDemand() {
    $('#modal').modal('handleUpdate');
    $('#modal').modal('show');
  }

  reload(reload: boolean) {
    if (reload) window.location.reload();
  }

}
